.section {
  min-height: calc(100vh - (148px + 128px));
  background-color: rgb(242, 245, 247);
}

.wrapperInput {
  display: flex;
  margin-bottom: 20px;
  gap: 20px;
}
.textFilter {
  padding: 50px;
  text-align: center;
  font-family: 'DM-Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
}
.dark {
  background-color: #23272f;
  color: white; 

  border-bottom: 1px solid white;
}

.light {
  background-color: rgb(242, 245, 247);
}
